.react-bs-table-container table {
  margin-bottom: 0rem;
  table-layout: fixed;
  height: 1px;
}

.react-bs-table table td,
.react-bs-table table th {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.react-bs-table table th {
  padding: 2px;
  vertical-align: middle;
}

.react-bs-table .table > :not(:first-child) {
  border-top: 1px solid #bdbdbd;
}
th.sort-column {
  cursor: pointer;
}
.react-bootstrap-table-page-btns-ul.pagination {
  justify-content: flex-end;
}

.react-bs-container-header > table {
  background: #f9fafb;
}
.react-bs-table .table td {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 0px;
  position: relative;
}
.react-bs-table .table td > div {
  position: relative;
  min-height: 35px;
  height: 100%;
  white-space: pre-line;
  /* white-space: pre-wrap; */
  word-break: break-word;
}
.react-bs-table .table tr.react-bs-container-row-expand > td > div {
  min-height: 0px;
}
