.a-scrwhhover{
  --scroll-size: 4px;
  --scroll-radius: 4px;
  --scroll-track: rgb(0 0 0 / 40%);
  --scroll-thumb-color: #dfdfdf;
}
.a-scrwhhover::-webkit-scrollbar {
  width: var(--scroll-size, 10px);
  height: var(--scroll-size, 10px);
}
.a-scrwhhover::-webkit-scrollbar-track {
  background-color: var(--scroll-track, transparent);
  border-radius: var(--scroll-track-radius, var(--scroll-radius));
}
.a-scrwhhover::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color, grey);
  background-image: var(--scroll-thumb, none);
  border-radius: var(--scroll-thumb-radius, var(--scroll-radius));
}
/* body */
body.a-scrwhhover{
  overflow: auto;
  overflow: overlay;
  --scroll-size: 8px;
  --scroll-radius: 8px;
  --scroll-track: transparent;
  --scroll-thumb-color: transparent;
}
body.a-scrwhhover.modal-open{
  overflow: hidden;
}
body.a-scrwhhover::-webkit-scrollbar-track  {
  --scroll-track: rgba(0,0,0,0.1);
}
body.a-scrwhhover::-webkit-scrollbar-thumb {
  --scroll-thumb-color: rgba(0,0,0,0.3);
}

/* in cell*/
div.react-bs-table td>.a-scrwhhover{
  --scroll-size: 4px;
  --scroll-radius: 4px;
  --scroll-track: transparent;
  --scroll-thumb-color: transparent;
}
div.react-bs-table td:hover>.a-scrwhhover::-webkit-scrollbar-track  {
  --scroll-track: rgba(0,0,0,0.1);
}
div.react-bs-table td:hover>.a-scrwhhover::-webkit-scrollbar-thumb {
  --scroll-thumb-color: rgba(0,0,0,0.3);
}
/* textarea */
div.react-bs-table td>.a-scrwhhover>textarea::-webkit-scrollbar {
  width: var(--scroll-size, 10px);
  height: var(--scroll-size, 10px);
}
div.react-bs-table td>.a-scrwhhover>textarea::-webkit-scrollbar-track {
  background-color: var(--scroll-track, transparent);
  border-radius: var(--scroll-track-radius, var(--scroll-radius));
}
div.react-bs-table td>.a-scrwhhover>textarea::-webkit-scrollbar-thumb {
  background-color: var(--scroll-thumb-color, grey);
  background-image: var(--scroll-thumb, none);
  border-radius: var(--scroll-thumb-radius, var(--scroll-radius));
}
div.react-bs-table td>.a-scrwhhover>textarea{
  --scroll-size: 4px;
  --scroll-radius: 4px;
  --scroll-track: transparent;
  --scroll-thumb-color: transparent;
}
div.react-bs-table td:hover>.a-scrwhhover>textarea::-webkit-scrollbar-track {
  --scroll-track: rgba(0,0,0,0.1);
}
div.react-bs-table td:hover>.a-scrwhhover>textarea::-webkit-scrollbar-thumb {
  --scroll-thumb-color: rgba(0,0,0,0.3);
}

/* for custom scr hover*/
